import React from 'react';
import './popUpInfo.css';
export default function PopUpInfo(props){
    let contentP=[];
    try {
        let content = props.content.content.split(',');
        contentP.push(<p key={0}>Course Content</p>)
        for(let i = 0 ; i< content.length;i++){
            contentP.push(<p key={i+1} style={{marginBottom:"4px"}}>- {content[i]}</p>)
        }
    } catch (error) {
        contentP=[];
    }
    return(
        <React.Fragment>
            <div className="PopUpInfo">
                <div className="titleWrapperPopUp">
                    <h2 className="titlePopUp">{props.content.title}</h2>
                    <p className="close" style={{color:"red",margin:"0"}} onClick={()=>props.close()}>x</p>
                </div>
                <div style={{padding: "20px"}}>
                    <p>{props.content.description}</p>
                    {contentP}
                </div>
            </div>
        </React.Fragment>
    )
}