import React,{useState,useEffect} from 'react';
import RawPage from './RawPage';
import QAData from './PCBQA.json';
import QATiles from '../QAComponents/QATiles';
import PCB from './PCB.jpg'
import './PCBQA.css';
import LandingImage from '../SharedComponents/LandingImage';

export default function PCBQA(){
    const [QA,updateQA]=useState(QAData);
    useEffect(()=>{
        updateQA(QAData);
    },[])
    return(
        <RawPage 
            content={
                <React.Fragment>
                     <LandingImage content={{
                        image:PCB,
                        title:"QA",
                        discritpion:"Common Questions and Answers"
                        }}/>
                    <QATiles content={QA}/>
                    <div style={{width:"100%",height:"5vh"}}></div>
                </React.Fragment>
            }
        />
    )
}