import React from "react";

export default function LandingImage(props){ 
    return(
        <React.Fragment>
            <div
            className="pic"
            style={{
                backgroundImage: `url(${props.content.image})`,
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
                backgroundPosition: "center",
                height: "100vh",
            }}
            >
              <div style={{textAlign: "center",paddingTop: "36vh"}}>
              <p style={{
                fontSize: "50px",
                fontWeight: "400",
                color: "white",
              }}>{props.content.title}</p>
              <p style={{
                 fontSize: "35px",
                 fontWeight: "300",
                 color: "#fff5d9bf",
              }}>{props.content.discritpion}</p>
              </div>
              </div>
        <div className="FilterFront" style={{
            width: "100%",
            height: "100vh",
            marginTop: "-100vh",
            backgroundImage: "linear-gradient(to right, #f3822187, #273bb091)"}}></div>
        </React.Fragment>
    )
}