import React from 'react';
import './QATiles.css';
import QATile from './QATile';
import MiniHeader from '../genericComponents/miniHeader';

export default function QATiles(props){
    let tiles=[]
    for(let i = 0 ; i<props.content.length;i++){
        tiles.push(
            <QATile key={i} content={{QA:props.content[i]}} />
        )
    }
    return(
        <React.Fragment>
            <MiniHeader content={{
               title:"PCB QA",
               discritpion:"Most Common QA in during PCB Design and Fabrication",
             }}/>
           {tiles}
        </React.Fragment>
    )
}