import React ,{useState,useEffect}from "react";
import "./HomePage.css";
import Items from "../HomePageComponents/items";
import RawPage from "./RawPage";
import test from './test.jpg';
import Cards from './HomePage.json';
import LandingImage from '../SharedComponents/LandingImage';
import MiniHeader from '../genericComponents/miniHeader';
import junior from '../courses/junior.json';
import rangers from '../courses/Rangers.json';
import engineering from '../courses/Engineering.json';
export default function HomePage() {
  const [cards,updateCards]=useState([]);
  useEffect(()=>{
      updateCards(Cards);
  },[])
  return (
    <RawPage
      content={
        <React.Fragment>
          <section className="Landing">
            <LandingImage content={{
              image:test,
              title:"Phoenix Camp",
              discritpion:"Transform yourself into a Ninja in technology"
            }}/>
          </section>
          <section>
             <MiniHeader content={{
               title:"Junior Courses",
               discritpion:"courses are for our future leaders age from 5 - 8 years",
               primaryColor:"#f9919b"
             }}/>
             <Items content={junior} type={"course"} />
          </section>
          <section>
             <MiniHeader content={{
               title:"Rangers Courses",
               discritpion:"courses are for the young people who are full of dreams and passion age from 7 - 14 years",
               primaryColor:"#149442"
             }}/>
             <Items content={rangers} type={"course"} />
          </section>
          <section>
             <MiniHeader content={{
               title:"Engineering Courses",
               discritpion:"courses are for our Future Engineers who are interested in latest technologies",
               primaryColor:"#024580"
             }}/>
             <Items content={engineering} type={"course"} />
          </section>
          <section className="ShowOff">
          <MiniHeader content={{
               title:"Hot Articles & Projects",
               discritpion:"Some of our finest Articles written by our instructors" 
             }}/>
            <Items content={cards} type={"project"} />
          </section>
          <div style={{height:"30px"}}>

          </div>
        </React.Fragment>
      }
    />
  );
}
