import React from "react";
export default function MiniHeader(props){
    let color= props.content.primaryColor
    return(
        <React.Fragment>
            <div style={{
                margin: "auto",
                width: "90vw",
                textAlign: "center",
                padding: "20px"
            }}>
                <h2 style={{color,fontSize:"27px"}}>{props.content.title}</h2>
                <p style= {{color:"#0000008c"}}>{props.content.discritpion}</p>

            <div className="breaker" style={{
               height: "4px",
               backgroundImage: "linear-gradient(to left, rgb(244, 235, 130), transparent)",
               borderRadius: "5px",
               marginLeft: "40%",
               marginRight: "40%",
               marginBottom: "4px",
            }}></div>
            </div>
        </React.Fragment>
    )
}