import React ,{useState} from 'react';
import './QATiles.css';

export default function QATile(props){
    const [tileView,toggleTileView]=useState(true);
    return(
        <React.Fragment>
            {
                tileView ?
                  <div className="QATileSmall" onClick={()=>toggleTileView(!tileView)}>
                        <p className="Question">Q: {props.content.QA.Q}</p>
                  </div>:
                  <div className="QATileExpanded" onClick={()=>toggleTileView(!tileView)}>
                      <div className="QuestionWrapper">
                        <p className="Question">Q: {props.content.QA.Q}</p>
                      </div>
                   
                    <p className="Answer">A: {props.content.QA.A}</p>
                </div>
            }   
     
        </React.Fragment>
    )
}